* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Quicksand", Verdana, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-wrapper{position:relative;margin:0 auto;width:100%;height:100%;display:grid;grid-template-rows:80px 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;grid-template-columns:80px 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr}.app-wrapper__content{grid-area:2/2 / -1/ -1;min-height:100vh}




.header{z-index:100;grid-area:1/2 / span 1/ -1;background-color:rgba(248,249,251,0.6);position:-webkit-sticky;position:sticky;top:0px;display:flex;justify-content:flex-end;align-items:center;flex-direction:row;padding:1rem}.header__logo{width:3rem;margin:0.4rem;margin-left:0.7rem}.header__auth-block{display:flex;justify-content:flex-end;align-items:center;flex-direction:row}.header__user{margin-right:1rem;font-family:"Quicksand",Verdana,sans-serif;font-weight:500}.header__login{color:#6f42c1;font-weight:700;font-size:larger}.header__logout-btn{display:flex;justify-content:center;align-items:center;flex-direction:row;background-color:transparent;border:none;padding:0;color:#343a40;outline:none;cursor:pointer;transition:all 0.4s ease-in-out}.header__logout-btn:hover{color:#6f42c1;transform:scale(1.1)}

.form-controler{font-size:0.8rem;position:relative;width:100%}.form-controler__textarea,.form-controler__input{border:none;padding:0;width:100%}.form-controler__error-message{position:absolute;background-color:#92212c;padding:0.5rem 0.4rem;display:inline-block;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;border-radius:5px;color:#f8f9fb;transform:translate(-100%, -30%);top:0.1rem;left:-0.3rem}.form-controler__error-message::after{content:"";position:absolute;border:7px solid transparent;border-left-color:#92212c;top:0.15rem;right:0;transform:translate(97%, 50%)}

.login{display:flex;justify-content:center;align-items:center;flex-direction:row;height:80%}.login__logo{min-width:40px;width:20%}.login__title{margin:1vw 0}.login__text{margin:0.3vw 0 0.5vw 0}.login__wrapper{min-width:25rem;min-height:28rem;width:28vw;height:30vw;padding:4rem 3rem;background-color:rgba(111,66,193,0.2);border-radius:3rem;display:flex;justify-content:flex-start;align-items:center;flex-direction:column}.login__form{align-self:stretch}.login__input-wrapper{display:flex;justify-content:flex-start;align-items:center;flex-direction:row;margin-bottom:0.6rem;position:relative}.login__input-wrapper>.form-controler>.form-controler__input{border-radius:50px;padding:0.85rem 1rem 0.85rem 3rem;background-color:rgba(248,249,251,0.8);color:#6f42c1;border:1px solid transparent;outline:none;transition:all 0.4s ease-in-out}.login__input-wrapper>.form-controler>.form-controler__input:focus{border:1px solid #6f42c1}.login__input-wrapper>.form-controler>.form-controler__input::-webkit-input-placeholder{color:#6f42c1}.login__input-wrapper>.form-controler>.form-controler__input:-ms-input-placeholder{color:#6f42c1}.login__input-wrapper>.form-controler>.form-controler__input::-ms-input-placeholder{color:#6f42c1}.login__input-wrapper>.form-controler>.form-controler__input::placeholder{color:#6f42c1}.login__input-wrapper>.form-controler_has-error>.form-controler__error-message{padding:0.5rem 0.4rem;border-radius:5px;transform:translate(-100%, 10%);top:0.1rem;left:-0.7rem}.login__input-wrapper>.form-controler_has-error>.form-controler__error-message::after{border:7px solid transparent;border-left-color:#92212c;top:0.15rem;right:0;transform:translate(97%, 50%)}.login__icon{position:absolute;left:0.3rem;border-radius:50%;padding:0.3rem;background-color:rgba(111,66,193,0.2);z-index:1;display:flex;justify-content:center;align-items:center;flex-direction:row}.login__checkbox-label{display:flex;justify-content:flex-start;align-items:center;flex-direction:row;margin-bottom:0.4rem}.login__checkbox-label>.form-controler{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.login__checkbox-label>.form-controler>.form-controler__input{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.login__btn{width:100%;background-image:linear-gradient(90deg, #8224e3 0, #a968ec 50%, #8224e3 100%);border:1px solid transparent;border-radius:50px;padding:1rem;color:#f8f9fb;outline:none;cursor:pointer;transition:all 0.4s ease-in-out}.login__btn:hover{border:1px solid #f8f9fb}.login__btn:active{border:1px solid #f8f9fb}

.Preloader_avaImg__2bHku img {
  width: 7rem;
  border-radius: 0.8rem;
  position: fixed;
  top: 50%;
  left: 50%;
}

.nav-bar{grid-area:1/1/-1 / span 1;display:flex;flex-direction:column;background-color:#f8f9fb}.nav-bar__logo{max-height:100%;max-width:100%;padding:16px;object-fit:contain;background-color:#343a40}.nav-bar__wrapper{display:flex;flex-direction:column;justify-content:flex-start;align-items:center;min-height:calc(100% - 80px);padding:2rem 0.4rem 0;font-size:0.5rem}.nav-bar__item{margin-bottom:2rem;transition:transform 2s linear;position:relative}.nav-bar__item:hover .nav-bar__tooltip{visibility:visible;opacity:1}.nav-bar__item a{color:#6c757d;transform:scale(1);transition:all 0.4s ease-in-out}.nav-bar__item a:hover{color:#6f42c1}.nav-bar__active-link{display:flex;justify-content:center;align-items:center;flex-direction:row;background-color:rgba(111,66,193,0.5);border-radius:0.2rem;padding:0.2rem;box-shadow:5px 10px 6px -6px rgba(111,66,193,0.5),-5px 10px 6px -6px rgba(111,66,193,0.5),0 10px 6px -6px rgba(111,66,193,0.5),0 10px 6px -6px rgba(111,66,193,0.5)}.nav-bar__active-link .nav-bar__tooltip{left:125%}.nav-bar__tooltip{visibility:hidden;font-size:0.9rem;width:80px;display:inline-block;background-color:#555;color:#fff;text-align:center;border-radius:8px;padding:5px 0;position:absolute;z-index:1;bottom:5%;left:120%;opacity:0;transition:opacity 0.4s}.nav-bar__tooltip::after{content:"";position:absolute;top:31%;left:-7%;margin-left:-5px;border-width:6px;border-style:solid;border-color:transparent #555 transparent transparent}

