@import "./common_styles/config";
.app-wrapper {
  position: relative;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: 80px 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-columns: 80px 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  // background-image: url("./assets/images/background-image.jpg");
  // background-size: contain;
  // background-repeat: no-repeat;
}

.app-wrapper__content {
  grid-area: 2/2 / -1/ -1;
  min-height: 100vh;
}
